import Section from "../section";
import "./content.css";
const Content=(props)=> {
	return (
		<div>
			<Section title="Tema">
				<p>Acessibilidade digital em sites e apps.</p>
			</Section>
			<Section title="Resumo">
				<p>O principal objetivo deste projeto de pesquisa  analisar os fatores por trs da diminuio da acessibilidade de sites e aplicativos na internet, buscando formas de interromper essa queda e conscientizar as empresas sobre a importncia desse tema, promovendo uma web mais diversificada e inclusiva.</p>
				<p>O projeto analisou diversos fatores histricos relacionados ao 
				funcionamento da web, comparando a forma em que os sites eram desenvolvidos antigamente com as tendncias e tecnologias do momento. Tambm foram consideradas diversas mudanas no setor de tecnologia nos ltimos anos, como a ascenso dos smartphones, a popularizao da inteligncia artificial, o desenvolvimento de ferramentas automatizadas para a criao de sites, a falta de investimento em acessibilidade por parte das empresas, entre outros. Tambm foram investigados os principais motivos que levam as empresas a no investir em acessibilidade digital, como a falta de resultados significativos a curto prazo, a falta de conhecimento sobre o tema, entre outros.</p>
				<p>Como forma de tentar diminuir o impacto e dimenso dos problemas encontrados na pesquisa, foi criada a ideia de um portal de avaliao da acessibilidade de diversos sites e apps distintos, com o objetivo de premiar empresas que apresentem interesse e comprometimento na rea, alm de fornecer uma maneira fcil para que os usurios encontrem plataformas adequadas ao seu uso. O objetivo  incentivar empresas a realizar esforos na rea, alm de reconhecer esforos que j esto sendo realizados.</p>
			</Section>
			<Section title="Problema">
				<p>Por que a acessibilidade digital tem diminudo em muitas plataformas e como isso pode ser revertido?</p>
			</Section>
			<Section title="Justificativa">
				<p>A primeira pergunta que fiz a mim mesmo ao iniciar este projeto foi: por que pesquisar sobre a diminuio da acessibilidade digital em sites e apps?</p>
				<p>Como sou um usurio de tecnologia com deficincia visual, o tema acessibilidade digital  de grande importncia para mim, pois ela  necessria para que eu possa usufruir das plataformas que utilizo diariamente. Como eu notei essa diminuio de acessibilidade em vrios sites e aplicativos que utilizo,  de meu interesse entender as causas desta diminuio e, claro, que algo possa ser feito para superar este problema.</p>
				<p>Alm disso, estou estudando desenvolvimento web e me aprofundei bastante na rea de acessibilidade, e percebi que a maioria dos sites no est seguindo as prticas recomendadas pela W3C (a comunidade internacional responsvel por manter o HTML e outras tecnologias web).</p>
				<p>Acredito que tratar deste tema tambm  de grande importncia para toda a sociedade, pois precisamos conscientizar as empresas sobre esse grupo de usurios e tornar a web mais acessvel. De acordo com um relatrio mundial feito pela ONU, mais de 1 bilho de pessoas vivem com algum tipo de deficincia.</p>
				<p>Mesmo que um determinado site no seja totalmente inacessvel e s tenha problemas em algumas partes, praticamente todos os usurios deficientes so prejudicados por pequenas mudanas que as empresas fazem sem pensar em todos os pblicos. A falta de acessibilidade est nos mnimos detalhes, nas pequenas alteraes.</p>
				<p>E mesmo que o nmero de pessoas afetadas por um problema seja pequeno,  importante pensar em todos os usurios e na experincia que eles tero com o seu site / aplicativo.</p>
			</Section>
			<Section title="Objetivos">
				<h3>Objetivo geral</h3>
				<p>entender os principais motivos da diminuio da acessibilidade em sites e apps e como revert-la.</p>
				<h3>Objetivos especficos</h3>
				<ol>
					<li>Pesquisar mais sobre o funcionamento das principais ferramentas usadas na criao de sites (Bootstrap, Wordpress, React JS, Angular, entre outras) e verificar se seguem os critrios/padres adequados.</li>
					<li>Conscientizar as empresas sobre a necessidade de se criar um site inclusivo e que permita igual acesso a todos.</li>
					<li>Criar um portal em que sero feitas avaliaes de vrios sites e apps quanto  acessibilidade, por uma equipe especializada.</li>
					<li>Incentivar o uso de sites/apps acessveis em diversos espaos, como escolas e locais de trabalho, por meio das avaliaes do portal. Exemplo: um professor deseja fazer um quis em uma de suas aulas para entreter e divertir os alunos, mas no sabe ao certo qual plataforma utilizar. Ele tem um aluno com deficincia motora que deve poder participar, ento ele entra na categoria quis do portal e verifica a avaliao dos sites apresentados. Se a avaliao estiver baixa, ele pensa: Opa,  melhor no usar esse site aqui.. Se a avaliao estiver com alta porcentagem, o professor saber que se trata de uma plataforma inclusiva e a utilizar normalmente.</li>
					<li>Utilizar o site para que as empresas interessadas possam melhorar seus sites e saber o que  ideal para seus usurios. Uma avaliao um pouco baixa no significa que a empresa no se preocupa com o tema, mas alguns pontos podem passar despercebidos durante o desenvolvimento.</li>
					<li>Valorizar o esforo e dedicao de empresas que se preocupam com a acessibilidade de alguma forma, como com uma premiao para os desenvolvedores com maiores porcentagens. Nem todas as empresas se preocupam com isso, e  importante reconhecer quem est se esforando em tornar sua plataforma inclusiva.</li>
				</ol>
			</Section>
			<Section title="Referencial terico resumido">
				<p>Este referencial terico no possui todas as pesquisas realizadas ao longo deste projeto. Para ler o referencial terico completo, <a href="projeto_de_pesquisa_acessibilidade_digital.pdf" target="_blank">Acesse o documento com o projeto completo (abre em uma nova guia)</a>.</p>
				<p>   Buscando concluir os objetivos de pesquisa para compreender da melhor maneira o tema geral da mesma visando organiz-la, foram realizadas diversas pesquisas sobre diferentes assuntos que a englobam, como o real significado de acessibilidade digital, qual a sua importncia para a sociedade e o que j foi feito sobre o tema.</p>
				<h3>O que  acessibilidade digital?</h3>
					<p>Na verdade, no existe uma definio totalmente precisa ou completa em relao ao termo acessibilidade digital. No entanto, podemos unir vrias citaes j existentes, e tentar formar uma definio mais clara.</p>
					<p>Segundo o site oficial do governo federal brasileiro (Brasil, 200?): Acessibilidade Digital  a eliminao de barreiras na Web. O conceito pressupe que os sites e portais sejam projetados de modo que todas as pessoas possam perceber, entender, navegar e interagir de maneira efetiva com as pginas.</p>
					<p>Pginas acessveis so mais facilmente indexadas por mecanismos de busca, so compatveis com uma maior variedade de aplicativos, alm de serem mais fceis e rpidas para navegar, beneficiando, assim, a todas as pessoas, incluindo pessoas idosas, sem habilidade para usar a internet, que utilizam dispositivos mveis etc.</p>
					<p>E, de acordo com a Universidade de Oregon, nos Estados Unidos (UO, 200?).</p>
					<p>(Em traduo livre: Acessibilidade digital  a prtica e a mentalidade de projetar tecnologias utilizveis pelo maior pblico possvel.  medida que a tecnologia se torna cada vez mais arraigada em nossas vidas dirias,  de suma importncia que implementemos tecnologias que apoiem a misso da universidade de promover a igualdade e a diversidade.)</p>
				<p>Unindo as duas definies, podemos concluir que a acessibilidade digital  uma prtica que se destina a permitir acesso igualitrio entre todas as pessoas, independente das suas limitaes e em qualquer tipo de tecnologia, como sites, aplicativos, ferramentas digitais e at mesmo equipamentos fsicos. Embora a acessibilidade digital foque principalmente nas pessoas com deficincia, ela tambm se aplica a pessoas idosas ou com outros tipos de dificuldade, ou mesmo uma dificuldade temporria (.</p>
				<h3>Por que esse tema precisa receber mais ateno?</h3>
				<p>Uma pesquisa conduzida pela Big Data Corp e pelo Movimento Web para Todos (MWPT), durante 2022, avaliou 21 milhes de sites ativos no Brasil. Deles, apenas 0,46% tiveram sucesso em todos os critrios avaliados. Isso mostra uma diminuio de 0,43% em relao  edio anterior, realizada em 2021, em que o nmero de sites acessveis havia aumentado.</p>
 <p>De acordo com a MWPT (2022), isso se deve principalmente ao crescimento de sites ativos, sobretudo os pequenos, nos quais a preocupao com a acessibilidade pode ser menor. O CEO da BigDataCorp, Thoran Rodrigues (Big Data Corp, 2022), afirma que, conforme aumenta a complexidade dos sites e da navegao, a tendncia de testes falharem fica cada vez maior.</p>
				<h3>Quais acontecimentos esto influenciando a acessibilidade digital e de que formas?</h3>
				<h4>Mudanas na web</h4>
				<p>Em geral, nos primeiros anos de existncia da web (quando as pginas ainda eram mais limitadas em seu tipo de contedo), era improvvel que algum site no tivesse acessibilidade digital. O contedo deles era basicamente textual, e o desenvolvimento de sites era mais restrito aos programadores/desenvolvedores (ou seja, no era to fcil para qualquer pessoa criar um site como hoje). Com a introduo de ferramentas automatizadas para criar sites sem escrever cdigo, como o WordPress, as pessoas comearam a criar pginas sem saber o que est por trs delas (a pessoa pode testar um site, notar que est bonito e funcionando e acreditar que a usabilidade ser adequada para todos, por exemplo). Ou em outros casos, no saber nem mesmo que existem ferramentas para que os deficientes naveguem na web. Segundo uma pesquisa conduzida pela W3Techs (, mais de 40% dos sites da internet so feitos em WordPress.</p>
				<p>WordPress is used by 43.2% of all the websites, that is a content management system market share of 63.3%.</p>
				<h4>Fechamento de apps populares</h4>
				<p>Um outro problema relacionado ao tema da acessibilidade digital est no fechamento cada vez maior de sites e apps. Isso impede o acesso por meios externos ou programas alternativos. Por exemplo, o Twitter, de acordo com sua prpria conta oficial administrada pelos desenvolvedores (Twitter Dev, 2023), est encerrando sua API gratuita, o que pode impedir muitos clientes de Twitter adaptados de funcionar, como o TW Blue.</p>
				<p>Isso dificulta a evoluo de solues especficas para cegos, como o Dosvox, que possui diversos programas para acesso a diferentes plataformas (o Cartavox para acesso a e-mail, o Webvox para acesso a sites web etc.</p>
				<h4>Interatividade em excesso nos sites e apps</h4>
				<p>Alm das dificuldades j citadas, existem outros problemas causados pela web atual, como o uso de interatividade em excesso nos sites e apps. Alguns exemplos so o uso de carrossis nos sites, menus dropdown projetados sem acessibilidade em mente, botes que funcionam apenas com o mouse, entre outros. Buscando compreender o impacto desses carrossis na usabilidade dos sites em diferentes tipos de deficincias, a MWPT realizou uma entrevista com Odilon Gonsalves, gerente de tecnologia e inovao do Museu da Pessoa e especialista em design acessvel () (2021).
				Odilon afirma: Eu sou a favor da eliminao de carrossis como os usados em muitas lojas virtuais, que so aqueles que tm muitos itens sequenciados, pouco tempo para ler cada destaque e aquelas microbolinhas que sinalizam qual deles est sendo exibido.</p>
				<p>Sou a favor que os carrossis sejam usados de forma pensada e acessibilizada. Ou seja, que sejam usados em casos em que no h outra alternativa, como em algumas experincias pontuais de navegao em exposies virtuais.</p>
				<h4>Uso e popularizao dos smartphones</h4>
				<p>Outro fator que pode influenciar bastante na acessibilidade digital  o uso de smartphones, que se tornou at mais comum que o uso do computador nos dias de hoje. De acordo com uma pesquisa realizada pela Fundao Getlio Vargas (FGV, 2022), em junho de 2022, o Brasil possui mais smartphones que habitantes no total. So cerca de 214 milhes de habitantes, segundo o IBGE, e 242 milhes de smartphones.</p>
				<p>Mas at que ponto isso influencia na acessibilidade para pessoas com deficincia? Na verdade, isso varia bastante de acordo com cada usurio, pois alguns conseguem se adaptar bem s telas sensveis ao toque, enquanto outros preferem o uso de um teclado fsico. Como os celulares tambm tm leitores de tela e outras tecnologias atualmente, o touch screen acaba no prejudicando tanto a experincia.</p>
				<p>Embora os smartphones, por si mesmos, no prejudiquem a acessibilidade digital, ainda  essencial que as empresas desenvolvam interfaces adequadas a ela.</p>
				<p>De acordo com A. G. T. S. Filho (: o design de informao tem como principal preocupao a percepo da informao pelos seres humanos, ou seja, como as pessoas traduzem o que veem e ouvem em conhecimento.</p>
			</Section>
			<Section title="Metodologia">
				<p>Este projeto de pesquisa incluiu diversos passos de realizao, como a criao de formulrios digitais de pesquisa e a busca de estudos bibliogrficos em fontes adequadas. Porm, o principal pilar para a realizao do projeto Accessibling  a sua aplicao tcnica, um website de mesmo nome. Neste site, os usurios de tecnologia com qualquer tipo de deficincia podero avaliar a acessibilidade de qualquer site ou app da sua escolha, enviando uma avaliao  pgina especfica da empresa desenvolvedora dentro da prpria aplicao. Para isso, a empresa dever estar cadastrada no catlogo de empresas do site, com dados como nome, CNPJ, e-mail para contato, endereo e outros. Cada usurio poder dar uma nota  acessibilidade do site ou app dessa empresa, podendo variar de 1 a 5 estrelas. O usurio tambm poder escrever um pequeno texto, explicando o que viu de positivo e negativo na acessibilidade daquele site ou app e o que pode ser feito para melhor-la. Se desejar, a empresa poder enviar uma resposta  avaliao realizada.</p>
				<p>Com base em todas as avaliaes que recebeu, cada empresa receber uma nota mdia, tambm em estrelas. O objetivo desse site de avaliao no  dar uma nota fixa e definitiva para cada empresa, mas sim acompanhar a evoluo ou mesmo queda de cada uma. Assim, se desejarem, os usurios podero trocar as avaliaes que realizaram. Assim, a avaliao antiga deixa de contar na nota mdia da empresa, e passa a contar ento a nova avaliao.</p>
				<p>Caso uma determinada empresa se destaque e tenha uma nota considerada muito boa, ela receber um selo oficialmente concedido pelo site, chamado selo de valorizao ao usurio. Ele mostra que a empresa tem compromisso e se importa com o tema da acessibilidade digital, alm de incentivar as empresas a continuarem seus esforos na rea, dando-lhes reconhecimento. Esse selo poder ser colocado no site oficial da empresa, como forma de divulgao, com um link para a pgina da companhia no site de avaliaes.</p>
				<p>A aplicao tcnica ser dividida em diversas pginas distintas, cada uma atendendo a uma funcionalidade especfica. Isso inclui uma pgina de login, uma pgina de criao de conta, uma pgina de cadastro de empresa, uma pgina de rankings, entre outras. O site tambm est sendo desenvolvido de forma a ser o mais acessvel possvel para pessoas com todos os tipos de deficincia.</p>
			</Section>
			<Section title="Concluses parciais">
				<p>Durante a realizao deste projeto de pesquisa, foi criado um formulrio de pesquisa com o objetivo de avaliar o conhecimento das pessoas da rea da programao sobre o tema acessibilidade digital. Esta pesquisa incluiu respostas de diversos pblicos distintos, como desenvolvedores, professores ou mesmo entusiastas da rea de programao. Ela foi realizada com um total de 44 participantes. Abaixo, esto as principais concluses obtidas com a pesquisa.</p>
					<ul>
						<li>Grande parte dos entrevistados j tinha ouvido falar sobre acessibilidade digital. Isso  algo positivo, considerando o que foi imaginado antes da pesquisa;</li>
						<li>O nmero de participantes que nunca realizaram nenhum teste de acessibilidade, embora no seja to elevado, abrange quase a metade dos participantes, o que ainda pode levar a problemas em muitos sites e apps;</li>
						<li>Um indicador bastante positivo foi que, entre os desenvolvedores front-end, 65,6% dos participantes j ouviram falar da especificao ARIA, que contm regras bastante importantes para a acessibilidade digital de sites web. Isso no era esperado antes da realizao da pesquisa;</li>
						<li>Muitos desenvolvedores no possuem nem mesmo o conhecimento para ajudar a melhorar a acessibilidade de um site ou app, o que foi dito por mais da metade dos participantes;</li>
						<li>A discusso da acessibilidade digital nos times de desenvolvimento varia bastante entre as empresas, com algumas que o discutem e outras que no. Porm, poucos entrevistados disseram que o tema  discutido com frequncia dentro das empresas em que trabalham;</li>
					</ul>
					<p>Os participantes tambm foram questionados sobre o que seria necessrio fazer para melhorar a acessibilidade digital nas empresas em que eles trabalhavam. As principais consideraes foram:</p>
					<ul>
						<li>Deve haver pessoas capacitadas ou pessoas com deficincia para testar/validar a acessibilidade;</li>
						<li>Demonstrar a necessidade e importncia da incluso, por exemplo colocar textos explicativos, no caso de um jogo;</li>
						<li>Conscientizao pblica, devendo ser replicada pelos times de comunicao da empresa, trazendo o tema para o censo comum;</li>
						<li>Talvez os contratantes (empresas) no possuam interesse no tema;</li>
						<li>Aprender a usar coisas mais simples, como texto alternativo em imagens e tags semnticas, pode em muitos casos tirar a necessidade de uso de ferramentas mais avanadas como ARIA;</li>
						<li>Incentivar o aumento de outros indicadores da empresa (alm do lucro);</li>
						<li>Presenciar os esforos que uma pessoa com deficincia faz para acessar o site ou app;</li>
						<li>Grandes empresas devem trazer o tema como requisito para vagas de emprego e em seus mdulos e certificaes;</li>
						<li>Mais canais e blogs que ensinem sobre o uso do ARIA e outros recursos, material escao em portugus e com fcil compreeno;</li>
					</ul>
					<p>Por fim, os participantes foram solicitados a deixar algum comentrio ou sugesto sobre o tema do formulrio. As principais consideraes foram:</p>
					<ul>
						<li>O questionrio deveria ser voltado a designers e no programadores, pois programadores apenas fazem o que  pedido, e designers que fazem sketchs e outros materiais;</li>
						<li>Um dos participantes tinha um app de dislexia para pessoas com dificuldade de leitura e escrita;</li>
						<li>Um dos participantes disse que a sua empresa fazia testes de acessibilidade com os 2 tipos de dautonismo (dificuldade ao distinguir vermelho e verde e azul e amarelo);</li>
						<li>As empresas e equipes de desenvolvimento devem entender que todos tm direito ao acesso, e que limitar o acesso limita a renda;</li>
						<li>A acessibilidade muitas vezes  proposta pelos times de UX/UI, mas no interessa ao cliente;</li>
						<li>Muitas vezes os sites so complicados demais, e os desenvolvedores esperam que as ferramentas encontrem todos os erros de acessibilidade;</li>
						<li>Um deles disse que na sua empresa, a acessibilidade  obrigada por lei (um banco pblico);</li>
						<li>Infelizmente a acessibilidade deve ser sim movida pela ganncia, dificilmente haver empatia real de gestores, desenvolvedores ou vendedores;</li>
						<li>Um dos participantes disse que mesmo no sendo da rea do front-end, estuda sobre o tema e busca aplicar a acessibilidade sempre que possvel.</li>
					</ul>
					<p>Como concluso sobre a pesquisa realizada, pode-se dizer que os indicadores so bastante divididos em relao ao tema, embora alguns deles tenham sido maiores que o esperado. Algo que precisa de mais ateno  a discusso ampla desse tema dentro dos times de desenvolvimento, alm de um maior conhecimento e conscientizao dos desenvolvedores sobre o tema, como atravs de treinamentos ou workshops feitos pelas empresas.</p>
			</Section>
			<Section title="Referncias resumidas">
				<p>Estas referncias no dizem respeito  todas as pesquisas realizadas no projeto, mas sim apenas s contidas neste site. Para ler todas as referncias, <a href="projeto_de_pesquisa_acessibilidade_digital.pdf" target="_blank">Acesse o documento com o projeto completo (abre em uma nova guia)</a>.</p>
				<p>BRASIL. Governo Federal Brasileiro. <b>Acessibilidade Digital.</b> 200?. Disponvel em: <a href="https://www.gov.br/governodigital/pt-br/acessibilidade-digital">https://www.gov.br/governodigital/pt-br/acessibilidade-digital</a>. Acesso em: 16/05/2023.</p>
				<br/>
				<p>UO. University of Oregon, EUA. <b>What is digital accessibility</b>. 200? Disponvel em: <a href="https://digitalaccessibility.uoregon.edu/about/whatisda">https://digitalaccessibility.uoregon.edu/about/whatisda</a>. Acesso em: 17/05/2023.</p>
				<br/>
				<p>MWPT, Movimento Web para Todos; BigDataCorp. <b>Nmero de sites brasileiros aprovados em todos os testes de acessibilidade tem queda em relao ao ano passado e  ainda menor que 1%</b>. 2022. Disponvel em: <a href="https://mwpt.com.br/numero-de-sites-brasileiros-aprovados-em-todos-os-testes-de-acessibilidade-tem-queda-em-relacao-ao-ano-passado-e-e-ainda-menor-que-1/">"https://mwpt.com.br/numero-de-sites-brasileiros-aprovados-em-todos-os-testes-de-acessibilidade-tem-queda-em-relacao-ao-ano-passado-e-e-ainda-menor-que-1/</a>. Acesso em: 17/05/2023.</p>
				<br/>
				<p>MWPT, Movimento Web para Todos; Big Data Corp. <b>Nmero de sites brasileiros aprovados em todos os testes de acessibilidade mantm crescimento, mas ainda  menos de 1% do total</b>. 22 de julho de 2021. Disponvel em: <a href="https://mwpt.com.br/numero-de-sites-brasileiros-aprovados-em-todos-os-testes-de-acessibilidade-mantem-crescimento-mas-ainda-e-menos-de-1-do-total/">https://mwpt.com.br/numero-de-sites-brasileiros-aprovados-em-todos-os-testes-de-acessibilidade-mantem-crescimento-mas-ainda-e-menos-de-1-do-total/</a>. Acesso em: 29 de novembro de 2023.</p>
				<br/>
				<p>TWITTER, Dev. <b>We will no longer support free access to the Twitter API, both v2 and v1.1.</b> 2 de fevereiro de 2023. Disponvel em: <a href="https://twitter.com/TwitterDev/status/1621026986784337922">https://twitter.com/TwitterDev/status/1621026986784337922</a>. Acesso em: 29/05/2023.</p>
				<br/>
				<p>GONSALVES, Odilon. MWPT. <b>Carrossis em sites so inimigos da acessibilidade?</b> 22 de novembro de 2021. Disponvel em: <a href="https://mwpt.com.br/carrosseis-em-sites-sao-inimigos-da-acessibilidade/">https://mwpt.com.br/carrosseis-em-sites-sao-inimigos-da-acessibilidade/</a>. Acesso em: 31/05/2023.</p>
				<br/>
				<p>Fundao Getlio Vargas (FGV). <b>Panorama do Uso de TI no Brasil - 2022.</b> Junho de 2022. Disponvel em: <a href="https://portal.fgv.br/artigos/panorama-uso-ti-brasil-2022">https://mwpt.com.br/carrosseis-em-sites-sao-inimigos-da-acessibilidade/</a>. Acesso em: 04/06/2023.</p>
				<br/>
				<p>FILHO, A. G. T. S. SANTOS, G. M. <b>A Ampliao da percepo da interface para pessoas com deficincia visual atravs de recursos em smartphones: Uma perspectiva do design da informao sobre a acessibilidade digital.</b> Setembro de 2015. Disponvel em: <a href="https://www.academia.edu/download/38997604/A_ampliacao_da_percepcao_da_interface_para_pessoas_com_deficiencia_visual_atraves_de_recursos_em_smartphones.pdf">https://www.academia.edu/download/38997604/A_ampliacao_da_percepcao_da_interface_para_pessoas_com_deficiencia_visual_atraves_de_recursos_em_smartphones.pdf</a>. Acesso em: 06/06/2023.</p>
				<br/>
			</Section>
		</div>
	);
}
export default Content;